.App {
  text-align: center;
}

.testModal {
  width: 400px;
  height: 400px;
  background-color: white;
  border: '2px solid #000';
  padding: 20px;
}

.wax{
  background-image: url('./imgs/wax.png');
  width: 375px;
  height: 667px;
}

.glass{
  background-image: url('./imgs/glass.png');
  width: 375px;
  height: 667px;
}

.materialPage{
  background-image: url('./imgs/material.png');
  width: 375px;
  height: 667px;
}

.spacer1{
  padding-top: 360px;
  padding-left: 30px;
  display: grid; 
  grid-template-columns: auto auto ;
  grid-column-gap: 20px;
  grid-row-gap: 30px;
}

